.lecturehallbox{display:flex;flex-direction:column;justify-content:space-between;margin:40px;margin-top:0}.lecturehallbox__title{width:fit-content;font-family:"Geniral";font-style:normal;font-size:380px;font-weight:400;line-height:424px;letter-spacing:.01em;text-transform:uppercase;color:#101010;white-space:nowrap;position:relative;animation:scroll-left 10s infinite linear}.lecturehallbox__title::after{position:absolute;content:"лекторий лекторий "}.lecturehallbox__title-inner{overflow:hidden;max-width:960px;width:100%}.lecturehallbox__title-wrapper{display:flex;justify-content:space-between;margin-bottom:-50px}.lecturehallbox__title-wrapper div:nth-child(2){margin-top:40px}.lecturehallbox__schedule{display:flex;justify-content:space-between;padding-top:40px;padding-bottom:30px;border-top:1px solid #bebebe}.lecturehallbox__schedule-date{width:160px;font-family:"Akrobat";font-style:normal;font-weight:600;font-size:20px;line-height:120%;text-transform:uppercase;color:#101010}.lecturehallbox__schedule-speakers{display:flex;gap:40px}@keyframes scroll-left{0%{transform:translateX(0)}100%{transform:translateX(-100%)}}