.btnbayticket{max-width:240px;background-color:#76fb9c;font-family:Akrobat;font-style:normal;font-weight:bold;font-size:20px;line-height:100%;text-transform:uppercase;color:#101010;padding-top:12px;padding-bottom:12px;display:flex;flex-direction:column;gap:6px;overflow:hidden;cursor:pointer}.btnbayticket span{white-space:nowrap;animation:scroll-left 0s infinite linear;position:relative}.btnbayticket span::after{position:absolute;content:" купить билет  купить билет "}.btnbayticket span:nth-child(2){animation:scroll-right 0s infinite linear;padding-right:100%}.btnbayticket span:nth-child(2)::before{position:absolute;content:" купить билет  купить билет ";right:100%}.btnbayticket:hover span{animation:scroll-left 7s infinite linear}.btnbayticket:hover span:nth-child(2){animation:scroll-right 7s infinite linear}@keyframes scroll-left{0%{transform:translateX(0)}100%{transform:translateX(-100%)}}@keyframes scroll-right{0%{transform:translateX(0)}100%{transform:translateX(100%)}}